var render = function render(){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"bgs",style:({ backgroundImage: 'url(bg/bglon2.webp),url(bg/bglon1.webp)' })},[_c('b-row',{staticClass:"fh"},[_c('b-col',{attrs:{"cols":"2"}},[_c('LeftCom')],1),_c('b-col',{attrs:{"cols":"8"}},[_c('b-row',{staticClass:"bookview"},[_c('div',{staticClass:"book"},[(_vm.kkk > 0)?_c('div',{staticClass:"pages",attrs:{"id":"pages"}},[_c('div',{staticClass:"page",class:_vm.getCl(0),style:(_vm.getZindex(0)),on:{"click":function($event){return _vm.clicks(0)}}},[_c('div',{staticClass:"book",style:({ backgroundImage: 'url(bg/a.png), url(' + _vm.anh + ')' })})]),_c('div',{staticClass:"page2",class:_vm.getCl(1),style:(_vm.getZindex(1))},[_c('Modau')],1),_c('div',{staticClass:"page scolls",class:_vm.getCl(2),style:(_vm.getZindex(2)),on:{"click":function($event){return _vm.clicks(2)}}},[_c('div',{staticClass:"box-content"},[_c('h2',{staticStyle:{"text-align":"center"}},[_c('span',{},[_vm._v("Sự tích hoa Cúc vạn thọ - Truyện cổ tích Việt Nam")])]),_c('h3',[_c('span',{},[_c('em',[_vm._v("Chuyện về một em bé thông minh và hiếu thảo, để có được tiền mua thuốc cho cha đã dùng trí của mình để giải được câu đó của tên nhà giàu. Nhân dịp dỗ mẹ, em đã sáng tạo nên một bông hoa để thể hiện tấm lòng của mình. Về sau thần Tiêu Lá đã lấy ý tưởng này tạo nên một bông hoa gọi là Vạn Thọ như lời chúc dành cho ông bà, cha mẹ được sống lâu.")])])]),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(3),style:(_vm.getZindex(3)),on:{"click":function($event){return _vm.clicks(3)}}},[_c('div',{staticClass:"box-content"},[_vm._v(" Ngày xưa có một em bé nghèo, mẹ mất sớm nên chỉ được sống với cha. Cha là chỗ để em nương tựa, nhưng cũng là người em phải chăm sóc, vì từ ngày mẹ không còn, cha em thường bị ốm đau luôn…"),_c('br'),_c('br'),_vm._v(" Lúc mẹ còn sống, em cũng được đi học dăm ba chữ. Sau khi mẹ mất, em đành học nghề để kiếm tiền nuôi cha. Em rất khéo tay nên đến học chạm trổ với một ông bác họ rất giỏi về nghề này."),_c('br'),_c('br'),_vm._v(" Năm đó cha em ốm khá nặng. Ông bác họ đã hết sức giúp đỡ nhưng sau đó đành chịu vì ông cũng nghèo. Ông mách cho em bé biết là ở vùng dưới có một tên nhà giàu đang cất nhà mới, cần thợ chạm trổ cột kèo. Em liền nhờ ông bác họ chăm sóc cha hộ rồi xách đồ nghề đi ngay. Em mong sẽ kiếm được một ít tiền về thuốc thang cho cha. Gặp em, tên nhà giàu cho biết là hắn đã thuê đủ thợ rồi. Nhưng hắn lại hỏi em:"),_c('br'),_c('br'),_vm._v(" -Bây giờ tao không cần thợ chạm trổ mà cần một người giúp tao chuyện khác."),_c('br'),_vm._v(" -Thưa ông chuyện gì?"),_c('br'),_vm._v(" -Mày biết ai có tài, cây chết rồi, vật chết rồi, người chết rồi, vẫn làm sống lại được thì mách cho tao, tao sẽ thưởng tiền và cho một ít thóc gạo."),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(4),style:(_vm.getZindex(4)),on:{"click":function($event){return _vm.clicks(4)}}},[_c('div',{staticClass:"box-content"},[_vm._v(" Tên nhà giàu này tiền của thì nhiều, nhưng lại không có một tí tẹo thông minh nào. Đã thế hắn lại thích tỏ ra ta cũng là người có chút ít chữ nghĩa và tài trí. Vì vậy, hắn thích chơi với những người có tài để học điều này điều nọ rồi đi khoe với bà con họ hàng hoặc với người này người kia. Nghe hắn bảo cần có một người có tài, cây chết rồi, vật chết rồi, người chết rồi, vẫn làm sống lại được, em bé liền nhìn ra sân, nhìn lên bàn thờ nhà hắn ta, ngẫm nghĩ một giây lâu rồi nói:"),_c('br'),_c('br'),_vm._v(" -Thưa ông, những việc ấy, ông có thể giao cho con…"),_c('br'),_vm._v(" -Con ông nào? Con nhà ai?"),_c('br'),_vm._v(" -Con chứ còn con nhà ai nữa?"),_c('br'),_vm._v(" -Mày ấy à?"),_c('br'),_vm._v(" -Dạ!"),_c('br'),_vm._v(" -Mày có thể cứu cây, cứu vật, cứu người chết rồi sống lại được à?"),_c('br'),_vm._v(" -Dạ!"),_c('br'),_vm._v(" -Mày tự làm à?"),_c('br'),_vm._v(" -Dạ!"),_c('br'),_vm._v(" -Mày làm không được thì sao?"),_c('br'),_vm._v(" -Thì con xin ở làm người giúp việc không công cho ông trong ba năm."),_c('br'),_vm._v(" -Được!"),_c('br'),_vm._v(" -Nhưng còn nếu con làm được thì sao?"),_c('br'),_vm._v(" -Tao sẽ thưởng cho mày một chục quan tiền và một chục nông thóc."),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(5),style:(_vm.getZindex(5)),on:{"click":function($event){return _vm.clicks(5)}}},[_c('div',{staticClass:"box-content"},[_vm._v(" Em bé liền chỉ ra một dây bầu sắp leo lên đến giàn ở ngoài sân tên nhà giàu và nói: “Xin ông cứ cắt ngọn, nhổ hết rễ cái dây bầu kia, con làm phép, dây bầu sẽ sống lại cho ông xem.”"),_c('br'),_c('br'),_vm._v(" Tên nhà giàu đần độn nghe nói liền làm theo ngay. Em bé cầm cái ngọn bầu, chùm rễ bầu ra về, nói chắc: “Sáng mai, mời ông cứ dậy sớm ra mà xem!”"),_c('br'),_c('br'),_vm._v(" Sáng hôm sau, tên nhà giàu gắng dậy sớm (vì hắn ta vốn quen thói dậy muộn) ra chỗ cái gốc bầu bị ngắt ngọn, cắt rễ thì đã thấy cây bầu sống lại thật. Khi em bé đến, hắn ta mở mắt ra mà nhìn em rồi khen:"),_c('br'),_c('br'),_vm._v(" -Hay! Hay! Mày giỏi lắm! Dây bầu sống lại thật rồi! Mày làm như thế nào, thử nói tao nghe!"),_c('br'),_vm._v(" -Thưa ông, để con cứu vật, cứu người, nhận thưởng của ông rồi con hãy nói luôn một thể."),_c('br'),_vm._v(" -Hay! Hay! Ừ! Như vậy cũng được!"),_c('br'),_c('br'),_vm._v(" Em bé lại chỉ con gà trống tơ đang đi trên sân rồi nói: “Thưa ông, ông cứ cho người nhà bắt con gà trống tơ kia thịt đi, sau đó ông cứ cho con bộ lông của nó con mang về nhà, ngày mai con sẽ mang nó sống lại nộp cho ông!”"),_c('br'),_c('br'),_vm._v(" Tên nhà giàu ngu ngốc liền sai gia nhân làm y lời em bé bảo. Em bé nhận bộ lông gà đủ các màu xanh trắng, tía ra về. Hôm sau em trở lại, mang theo một con gà trống tơ, đưa cho tên nhà giàu xem và nói: “Con đã làm phép cho nó sống lại rồi. ông xem có phải đúng là con gà nhà ông đã bị giết thịt hôm qua không?”"),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(6),style:(_vm.getZindex(6)),on:{"click":function($event){return _vm.clicks(6)}}},[_c('div',{staticClass:"box-content"},[_vm._v(" Tên nhà giàu đần độn ngu si càng trố mắt ra mà nhìn em bé, hắn nói:"),_c('br'),_c('br'),_vm._v(" – Hay! Hay! Đúng là nó rồi! Giỏi thật! Mày làm cách nào mà lại cứu sống được nó. Nói ngay cho tao nghe đi!"),_c('br'),_vm._v(" – Con đã nói từ đầu là xong cả ba việc, cứu cây, cứu vật, cứu người, nhận xong tiền thưởng, thóc thưởng con mới nói kia mà!"),_c('br'),_c('br'),_vm._v(" Tên nhà giàu đành phải nhượng bộ lần nữa. Em bé liền nhìn lên bàn thờ tên nhà giàu nói:"),_c('br'),_c('br'),_vm._v(" -Bây giờ ông cho con mượn cái bức vẽ ông cụ nhà, để con đem về một ngày, sáng mai con sẽ mang ông cụ sống lại đến cho ông!"),_c('br'),_c('br'),_vm._v(" Tên nhà giàu ngu ngốc lại làm đúng theo lời em bé. Hắn ta vừa bàng hoàng, vừa kinh ngạc hỏi lại:"),_c('br'),_c('br'),_vm._v(" – Ngày mai, chú sẽ đưa ông cụ tao sống lại đến đây à?"),_c('br'),_vm._v(" – Dạ!"),_c('br'),_c('br'),_vm._v(" Tên nhà giàu cứ đứng ngẩn người ra mà nhìn em bé mang cái bức vẽ ra về… Hôm sau em bé cầm bức vẽ trở lại, tên nhà giàu hỏi ngay:"),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(7),style:(_vm.getZindex(7)),on:{"click":function($event){return _vm.clicks(7)}}},[_c('div',{staticClass:"box-content"},[_vm._v(" – Ông cụ nhà tao sống lại chưa?"),_c('br'),_vm._v(" – Dạ rồi!"),_c('br'),_vm._v(" – Sao chưa thấy ông cụ đâu cả! Em bé liền chỉ vào cuộn giấy nói:"),_c('br'),_vm._v(" – Dạ, ở trong này rồi! Nói xong em bé mở cuộn giấy ra. Tên nhà giàu trợn mắt hỏi:"),_c('br'),_vm._v(" – Thế này mà gọi là sống à?"),_c('br'),_vm._v(" – Dạ!"),_c('br'),_vm._v(" – Mày điên à?"),_c('br'),_vm._v(" – Dạ không! Thưa ông vậy ông bảo cụ này chết à?"),_c('br'),_vm._v(" – Mày bảo ông ấy không chết mà sống đấy à ? – Em bé điềm tĩnh trả lời:"),_c('br'),_vm._v(" – Dạ, sống thật đấy chứ! Bởi vì người chết thì phải nhắm mắt. Mắt ông cụ vẫn mở to thế này, sao lại bảo là chết! Và người chết thì làm sao cười được. ông cụ cười thế này mà lại bảo là chết rồi sao?"),_c('br'),_c('br'),_vm._v(" Như thế là em bé đã vẽ lại bức tranh, ông cụ trong bức vẽ mới lại hơi mỉm cười. Tên nhà giàu đần độn, ngu ngốc không biết làm sao đành phải chịu thua cuộc em bé. Lão ta còn khen:"),_c('br'),_c('br'),_vm._v(" – Hay! Hay! Mày khôn lắm! ông phải chịu là mày giỏi… Nhưng còn cái con gà trống tơ và dây bầu thì mày đã làm như thế nào?"),_c('br'),_vm._v(" – Ông cứ mang tiền và thóc thưởng ra đây, con nhận xong, con sẽ nói."),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(8),style:(_vm.getZindex(8)),on:{"click":function($event){return _vm.clicks(8)}}},[_c('div',{staticClass:"box-content"},[_vm._v(" Tên nhà giàu liền vào lấy tiền và sai người nhà đong thóc thưởng cho em bé. Nhận thưởng xong, bấy giờ em bé mới nói:"),_c('br'),_c('br'),_vm._v(" – Thưa ông, cái dây bầu kia sống lại là nhờ thế này. Con đã chọn một cái dây bầu giống như cái dây bầu nhà ông, đang đêm mang đến trồng lại, chỉ có thế thôi! – Tên nhà giàu nghe nói cứ lặng người đi, vì xấu hổ. Em bé lại nói tiếp:"),_c('br'),_c('br'),_vm._v(" – Còn con gà trống tơ, thưa ông, nó cũng na ná như vậy. Con đã để ý và biết là trong xóm con có một con gà tía, rất giống con gà mà ông đã cho thịt. Gà rất nhiều con trông giống nhau, ông muốn có con nữa, con sẽ có ngay cho ông. Con bỏ tiền ra mua đem nộp cho ông rồi sẽ có tiền thưởng to hơn gấp nhiều lần mang về."),_c('br'),_c('br'),_vm._v(" Tên nhà giàu bấy giờ mới thấy hết cái đần độn, ngu ngốc của mình. Lão ta ôm đầu than thở: “Chỉ có vậy mà mình chẳng đoán ra! Nhưng dù sao thằng bé này vẫn là đứa giỏi. Thưởng cho nó cũng phải lắm.”"),_c('br'),_c('br'),_vm._v(" Nghĩ đến chuyện rồi mình sẽ bắt chước em bé và sẽ có khối người phục mình, hắn vui vẻ nhìn em bé nhận tiền, nhận thóc ra về. Có lẽ trong việc này, hắn đã tỏ ra bớt phần ngu ngốc. Thấy con mang mười quan tiền và mười nông thóc về, người cha mừng rỡ lạ lùng. Nghe con kể lại chuyện, ông đang ốm cũng phải cười khẽ mấy tiếng… Người cha vừa khỏi bệnh thì tiền kia thóc kia cũng không còn nữa. Mà ngày giỗ mẹ sắp đến rồi. ông liền nói với con:"),_c('br'),_c('br'),_vm._v(" -Thôi con ạ, cứ nấu bát cơm, kho đĩa cá cúng mẹ là được rồi!"),_c('br'),_c('br'),_vm._v(" Nhưng em bé hiếu thảo nào đâu chịu vậy. Em lại nghĩ đến tên nhà giàu tuy đần độn, ngu ngốc nhưng không đến nỗi keo kiệt kia. Em nghĩ bụng: Lần trước ông ta đố mình, bây giờ mình có cái gì đố lại nhỉ! Em định vậy nhưng chưa nghĩ ra được cách nào. Trước mắt em phải lo giỗ mẹ cái đã. ừ thì một bát cơm, một đĩa cá, nhưng cũng có tí hương hoa cho mẹ vui lòng. Em sẽ gắng tìm mua một nén hương. Còn hoa? Đang mùa cây khô, lá vàng, cả xóm chẳng thấy cái hoa nào để xin cả. Không có thì phải làm ra vậy!"),_c('br'),_c('br'),_vm._v(" Em bé vốn khéo tay lại vô cùng thông minh ấy liền chọn những lá lúa to đẹp nhất, cắt ra thành những sợi thật nhỏ rồi bó túm lại, bên dưới thắt thật chặt, còn bên trên thì cho xòe ra. Em lấy kéo tỉa thật đẹp, thật tròn. Sau đó em lại đi hái mấy cái lá xanh, buộc thêm vào ở dưới nhìn như cái đài hoa. Em làm một chùm năm cái, cắm vào ống tre, đặt lên bàn thờ cúng mẹ. Người cha thấy thế liền hỏi con:"),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(9),style:(_vm.getZindex(9)),on:{"click":function($event){return _vm.clicks(9)}}},[_c('div',{staticClass:"box-content"},[_vm._v(" -Con có thứ hoa gì mà lạ vậy? Em bé mỉm cười, ngẫm nghĩ một lát rồi đáp:"),_c('br'),_vm._v(" -Hoa trăm tuổi đó mà cha! Hoa để cúng mẹ rồi mẹ sẽ phù hộ cho cha sống thật lâu với con đó!"),_c('br'),_vm._v(" -Mới nhìn, cha cứ tưởng là hoa thật."),_c('br'),_c('br'),_vm._v(" Lời nói của người cha bỗng làm cho em bé nảy sinh ra một ý mới. Vậy là sau khi cúng mẹ, em liền đem một bông hoa kia đến tên nhà giàu và nói:"),_c('br'),_c('br'),_vm._v(" – Tôi có bông hoa lạ mang đến để biếu ông và cám ơn ông đã thưởng cho tôi tiền, thóc dạo nào. – Tên nhà giàu vui vẻ nhận hoa rồi hỏi lại:"),_c('br'),_vm._v(" – Hoa này là hoa thật hay hoa giả?"),_c('br'),_vm._v(" – Muốn thật thì nó thật mà muốn giả thì nó giả."),_c('br'),_vm._v(" – Vì sao lại thật? Vì sao lại giả?"),_c('br'),_vm._v(" – Thật vì nó có thật mà giả vì nó không phải từ cây mà từ tay người làm ra. Em bé trả lời xong tiếp luôn:"),_c('br'),_vm._v(" – Lần trước ông ra bài cho con làm. Lần này con xin phép được đố ông một câu vì con nghe nói ông giỏi về chuyện giải các câu đố lắm! – Tên nhà giàu nghe nói, gan ruột cứ nở nang cả ra."),_c('br'),_vm._v(" – Ừ, đố đi!"),_c('br'),_vm._v(" – Con đố ông hoa này có bao nhiêu cánh. ông mà đoán trúng, con sẽ đến ở giúp việc không công cho ông ba tháng."),_c('br'),_vm._v(" – Còn nếu không đoán đúng?"),_c('br'),_vm._v(" – Điều đó xin tùy ông! Con chỉ xin nói là hiện nay con đang cần vải để may cho cha con một bộ quần áo mới. Tết sắp đến rồi."),_c('br'),_vm._v(" – Được, tao mà đoán sai, tao sẽ cho mày mấy thước vải về may áo cho cha mày."),_c('br'),_vm._v(" – Dạ, vậy thì ông đoán đi! Tên nhà giàu nhìn vào bông hoa một lúc rồi nói:"),_c('br'),_vm._v(" – Tao đếm có được không?"),_c('br'),_vm._v(" – Đếm thì không phải là đoán nữa rồi. Nhưng xin ông cứ đếm."),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(10),style:(_vm.getZindex(10)),on:{"click":function($event){return _vm.clicks(10)}}},[_c('div',{staticClass:"box-content"},[_vm._v(" Tên nhà giàu thử đếm mà không tài nào đếm được vì cánh hoa nhiều quá, cứ xúm xít xen lẫn vào. Không làm sao biết rõ cánh hoa nào đã đếm, cánh hoa nào chưa đếm."),_c('br'),_c('br'),_vm._v(" -Tao mở ra đếm có được không?"),_c('br'),_vm._v(" -Mở ra thì càng không phải là đoán nữa rồi. Nhưng xin ông cứ mở."),_c('br'),_c('br'),_vm._v(" Tên nhà giàu liền mở bông hoa ra, bắt đầu đếm. ông ta đếm khá vất vả. Vì những cánh hoa bé và nhiều quá. Có lúc vừa đếm xong được một khóm gió bỗng thổi tung làm lẫn những cánh hoa chưa đếm vào. Có lúc chính ông ta quên mất là mình đã đếm đến chục thứ mấy, tám mươi hay chín mươi… Nhưng ông ta vẫn quyết đếm cho kỳ được. ông đếm mãi, gần hết cả buổi sáng mới gọi là xong. ông ta hớn hở trả lời luôn:"),_c('br'),_c('br'),_vm._v(" – Bông hoa này có vừa đúng một nghìn cánh! Em bé liền hỏi lại:"),_c('br'),_vm._v(" – Ông bảo là một nghìn cánh?"),_c('br'),_vm._v(" – Ừ! Chứ sao nữa! Mày đã chịu thua tao chưa nào?"),_c('br'),_vm._v(" -Vậy thì ông sai, sai to rồi!"),_c('br'),_vm._v(" -Sao lại sai! Mày đếm đi!"),_c('br'),_vm._v(" -Thưa ông tôi đã đếm rồi! Phải nói là ông đã đếm đúng nhưng mà vẫn sai."),_c('br'),_vm._v(" -Thằng này nói lạ! Đúng mà lại sai! Như vậy là thế nào?"),_c('br'),_vm._v(" -Dạ vì ông đã quên rằng: Hoa nào thì cũng có cánh và có nhụy, ông đã đếm cả cánh và nhụy gộp vào với nhau rồi."),_c('br'),_vm._v(" -Cái nào là cánh, cái nào là nhụy?"),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(11),style:(_vm.getZindex(11)),on:{"click":function($event){return _vm.clicks(11)}}},[_c('div',{staticClass:"box-content"},[_vm._v(" -Thưa ông, những cái ngắn hơn nhỏ hơn một chút là nhụy. Đây con sẽ chỉ cho ông xem. Em bé liền nhặt ra những sợi rơm nhỏ hơn và ngắn hơn một tí rồi bảo:"),_c('br'),_vm._v(" -ông xem đấy là nhụy đâu phải là cánh!"),_c('br'),_vm._v(" -Nhụy thì nó nằm ở giữa, sao nhụy này lại lẫn lung tung?"),_c('br'),_vm._v(" -Dạ, vì bông hoa này gồm nhiều hoa nhỏ ghép lại, mỗi hoa có bốn cánh và một nhụy ở giữa."),_c('br'),_vm._v(" -Vậy theo mày có bao nhiêu cánh, bao nhiêu nhụy?"),_c('br'),_vm._v(" -Tám trăm cái cánh, hai trăm cái nhụy! – Một lần nữa, tên nhà giàu lại phải chịu thua em bé."),_c('br'),_vm._v(" -Thằng này nói phải! Hoa thì phải có nhụy chứ! Tao quên, tao quên! Được, tao sẽ thưởng cho mày! Nhưng mày phải buộc lại cái bông hoa này để nó cho tao! Tao sẽ đem đi đố người khác xem họ có bị quên như tao không? Tao được cuộc, tao sẽ còn được to gấp mấy mày!"),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(12),style:(_vm.getZindex(12)),on:{"click":function($event){return _vm.clicks(12)}}},[_c('div',{staticClass:"box-content"},[_vm._v("Em bé nhận được mấy thước vải về may áo Tết cho cha. Em kể hết đầu đuôi câu chuyện cho cha nghe. Lần này không còn bị ốm như lần trước, người cha đã bật lên cười thành tiếng. Ăn Tết xong, người bác họ đi làm ở xa về bảo cho hai cha con biết là vua đang dựng một ngôi đền lớn và cần có nhiều thợ chạm trổ thật khéo tay. Ông rủ hai bố con em bé cùng đi với mình, hứa sẽ đưa em vào một nhóm người chuyên chạm trổ chân cột đền. Hai bố con cùng về kinh. Sau này, nhờ khéo tay, lại thông minh sáng tạo, em bé trở thành người thợ chạm trổ giỏi nhất nước."),_c('br'),_c('br'),_vm._v(" Một lần người thợ tài giỏi ấy được gặp cháu thần cây là thần Tiêu Lá. Nghe người thợ tài giỏi nhất nước kể lại câu chuyện ngày nhỏ, thần Tiêu Lá liền hỏi thật kỹ, sau đó thần tạo nên một giống hoa để người đời nhớ mãi tấm lòng của chú bé hiếu thảo rất đỗi thông minh kia. Hoa có hình dáng rất giống cái bông hoa kết bằng rơm của em bé ngày xưa. Hoa cũng có màu vàng như rơm, đẹp tươi hơn rơm, mà cũng nở vào dịp Tết. Hoa nở nhiều bông, lâu tàn, có hương thơm đặc biệt như là hương thơm của lòng hiếu thảo. Lá cũng thơm, đẹp như được một người rất khéo tay cắt tỉa chạm trổ. Hoa ấy ngày nay ta gọi là hoa Vạn Thọ. Tên có hai chữ nhưng đó là cả một lời chúc cho ông bà, cha mẹ và tất cả mọi người ai ai cũng được sống lâu trăm tuổi với những người thân của mình."),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(13),style:(_vm.getZindex(13)),on:{"click":function($event){return _vm.clicks(13)}}},[_c('div',{staticClass:"box-content"},[_vm._v(" Nguồn: Truyện cổ tích Tổng hợp. ")])]),_c('div',{staticClass:"page",class:_vm.getCl(14),style:(_vm.getZindex(14)),on:{"click":function($event){return _vm.denketthuc()}}},[_c('KetThuc')],1)]):_vm._e()])])],1),_c('b-col',{attrs:{"cols":"2"}},[_c('RightCom')],1)],1)],1)
