  }),
  metaInfo: {
    title: 'Sự tích hoa Cúc vạn thọ - Truyện cổ tích Việt Nam',

    meta: [
      { charset: 'utf-8' },
      { name: 'Keywords', content: 'Sự tích hoa Cúc vạn thọ' },
      { name: 'Description', content: 'Chuyện về một em bé thông minh và hiếu thảo, để có được tiền mua thuốc cho cha đã dùng trí của mình để giải được câu đó của tên nhà giàu. Nhân dịp dỗ mẹ, em đã sáng tạo nên một bông hoa để thể hiện tấm lòng của mình. Về sau thần Tiêu Lá đã lấy ý tưởng này tạo nên một bông hoa gọi là Vạn Thọ như lời chúc dành cho ông bà, cha mẹ được sống lâu.' },
